import { Button, Grid } from '@material-ui/core'
import React, { Component } from 'react'

export default class Counter extends Component {
    state={
        counter:0
    }
onIncrement=()=>{
    this.setState({
        counter: this.state.counter+1
    })
}
onDecrement=()=>{
    this.setState({
        counter:this.state.counter-1
    })

}
    render() {
    return (
            <Grid container style={{padding:250}} >
                <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
                    
                </Grid>
                <Grid item xs-12 sm-4 md-4 lg-4 xl-4xs={12} sm={4} md={4} lg={4} xl={4}>
                    <Button variant='contained' color='primary' fullWidth onClick={this.onIncrement}>+</Button>
                    <p fullWidth>{this.state.counter}</p>
                    <Button variant='contained' color='secondary' fullWidth onClick={this.onDecrement}>-</Button>
                </Grid>
                <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>

                </Grid>
            </Grid>
            
    
    )
  }
}
