import { AppBar, Toolbar, Typography } from '@material-ui/core'
import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

export default class Header extends Component {
  render() {
    return (
      <div>
          <AppBar>
              <Toolbar>
                  <Typography style={{padding:20}}>Counter App</Typography>
                  <NavLink style={{padding:20, color:'#fff' }} to='/'>Counter</NavLink>
                  <NavLink style={{padding:20, color:'#fff'}} to='/demo'>Demo</NavLink>
              </Toolbar>
          </AppBar>
      </div>
    )
  }
}
