import { Button } from '@material-ui/core'
import React, { Component } from 'react'
import Mydemocounter from './Mydemocounter'

export default class Demo extends Component {
    state={
        name:"Joel",
        age:"22"
    }
    changemyname=()=>{
        this.setState({
            name:"Prince",
            age:"27"
        })   
    }
  render() {
    return (
        <div >
            <h1>Props Exapmle</h1><br/>
            <p>Name: {this.state.name}</p>   
            <p>Age: {this.state.age}</p>   

            <Button onClick={this.changemyname} color='primary' variant='contained' >Next</Button>
            <Mydemocounter myname={this.state.name} myage={this.state.age}/>
        </div> 
    )
  }
}
